import { notFound } from "next/navigation";
import VideoPlayer from "@/components/VideoPlayer";
import ProjectNavigation from "@/components/ProjectNavigation";
import { getProjects, getProjectBySlug } from "@/app/actions/projects";
import { Project } from "@/lib/data";
import MoreLikeThis from "@/components/MoreLikeThis";

interface ProjectPageProps {
    params: Promise<{ slug: string }>;
}

export async function generateStaticParams() {
    const projects = await getProjects();
    return projects.map((project) => ({
        slug: project.slug,
    }));
}

export default async function ProjectPage({ params }: ProjectPageProps) {
    const { slug } = await params;

    const projects = await getProjects();

    const projectIndex = projects.findIndex((p) => p.slug === slug);
    const project = projects[projectIndex];

    if (!project) {
        notFound();
    }

    // Calculate Next Project
    const nextProjectIndex = (projectIndex + 1) % projects.length;
    const nextProject = projects[nextProjectIndex];

    // Filter "More Like This"
    // 1. Same category, exclude current
    let relatedProjects = projects.filter(
        (p) => p.category === project.category && p.id !== project.id
    );

    // 2. If less than 3, fill with others (excluding current and already added)
    if (relatedProjects.length < 3) {
        const remaining = projects.filter(
            (p) => p.id !== project.id && !relatedProjects.find((rp) => rp.id === p.id)
        );
        relatedProjects = [...relatedProjects, ...remaining].slice(0, 4);
    }

    // Cast for UI compatibility until loose types are fixed
    const relatedUI: any[] = relatedProjects;

    return (
        <main className="min-h-screen pt-32 pb-0 bg-black text-white">
            <div className="px-4 md:px-10 max-w-[1920px] mx-auto">
                {/* ... existing sections ... */}

                {/* Video Player Section */}
                <section className="mb-16">
                    <VideoPlayer thumbnail={project.thumbnail} videoUrl={project.videoUrl || undefined} />
                </section>

                {/* Info Grid */}
                <section className="grid grid-cols-1 md:grid-cols-12 gap-10 mb-32">
                    {/* Title & Meta - Left Col */}
                    <div className="md:col-span-4 lg:col-span-3">
                        <h1 className="text-3xl md:text-5xl font-sans font-bold uppercase mb-4 leading-tight">
                            {project.title}
                        </h1>
                        <div className="font-mono text-xs text-gray-400 space-y-1">
                            <p>{project.category}</p>
                            <p>{project.year}</p>
                        </div>
                    </div>

                    {/* Description & Credits - Right Col */}
                    <div className="md:col-span-8 lg:col-span-6 lg:col-start-5 space-y-10">
                        {project.description && (
                            <div className="text-lg md:text-xl font-light leading-relaxed text-gray-200">
                                <p>{project.description}</p>
                            </div>
                        )}

                        {/* Dynamic Credits */}
                        {project.credits && (
                            <div className="grid grid-cols-2 gap-8 pt-8 border-t border-white/10">
                                {JSON.parse(project.credits).map((credit: any, index: number) => (
                                    <div key={index}>
                                        <h4 className="font-mono text-xs text-gray-500 mb-2 uppercase">{credit.role}</h4>
                                        <p className="font-sans text-sm">{credit.name}</p>
                                    </div>
                                ))}
                            </div>
                        )}
                    </div>
                </section>

                {/* More Like This Carousel */}
                <MoreLikeThis projects={relatedUI} />

                {/* Footer Navigation */}
                <ProjectNavigation nextProject={nextProject} />
            </div>
        </main>
    );
}
